<template>
    <div class="all">
        <div class="back">
            <div class="change">
                <ul>
                    <li @click="tj"><i class="el-icon-plus"></i> 添加</li>
                    <li @click="bj"><i class="el-icon-edit-outline"></i> 编辑</li>
                    <li @click="sc"><i class="el-icon-delete"></i> 删除</li>
                </ul>
            </div>
            <el-tree class="tree" :data="data" :props="defaultProps" @node-click="handleNodeClick"></el-tree>
            <!-- <el-button class="xq" type="success">查看此详情数据</el-button> -->
        </div>
        <div class="table2">
            <div>
                <el-table
                    :data="tableData.slice((currpage - 1) * pagesize, currpage * pagesize)"
                    border
                    style="width: 100%">
                    <el-table-column
                    label="姓名"
                    width="180">
                    <template slot-scope="scope">
                        <span>{{ scope.row.name }}</span>
                    </template>
                    </el-table-column>
                    <el-table-column
                    label="性别"
                    width="180">
                    <template slot-scope="scope">
                        <span>{{ scope.row.gender == 1 ? "男":"女" }}</span>
                    </template>
                    </el-table-column>
                    <el-table-column
                    label="手机号"
                    width="180">
                    <template slot-scope="scope">
                        <span>{{ scope.row.mobile }}</span>
                    </template>
                    </el-table-column>
                    <el-table-column
                    label="微信号"
                    width="180">
                    <template slot-scope="scope">
                        <span>{{ scope.row.wx_num }}</span>
                    </template>
                    </el-table-column>
                    <el-table-column
                    label="职务"
                    width="180">
                    <template slot-scope="scope">
                        <span>{{ scope.row.zw }}</span>
                    </template>
                    </el-table-column>
                </el-table>
            </div>
            <div class="block">
                <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="currentPage"
                :page-sizes="[2, 3, 5, 10]"
                :page-size="pagesize"
                layout="total, sizes, prev, pager, next, jumper"
                :total="total">
                </el-pagination>
            </div>
        </div>
        <div class="tmask" ref="tmask">
            <div class="tj">
                <h3>添加分类标准</h3>
                <el-input
                class="input"
                placeholder="请输入内容"
                v-model="input1"
                ref="tj"
                clearable>
                </el-input><br/>
                <el-button class="btn1" @click="quxiao1">取消</el-button>
                <el-button type="primary" @click="tqd">确定</el-button>
            </div>
        </div>
        <div class="bmask" ref="bmask">
            <div class="bj">
                <h3>编辑标准分类</h3>
                <el-input
                class="input2"
                placeholder="请输入内容"
                v-model="input2"
                ref="bj"
                clearable>
                </el-input><br/>
                <el-button class="btn2" @click="quxiao2">取消</el-button>
                <el-button type="primary" @click="bqd">确定</el-button>
            </div>
        </div>
        <div class="smask" ref="smask">
            <div class="sc">
                <h3>确认删除此标准吗</h3>
                <el-button class="btn3" @click="quxiao3">取消</el-button>
                <el-button type="primary" @click="sqd">确定</el-button>
            </div>
        </div>
    </div>
</template>

<script>
import axios from 'axios';
export default {
    name:"jigou",
    inject:["reload"],
    data(){
        return{
            input1:"",
            input2:"",
            currentPage: 1,
            total:100,
            pagesize:10,
            currpage:1,
            defaultProps:{
                label:"dept_name",
                children:"children"
            },
            tableData: [],
            data: []
        }
    },
    methods: {
        handleNodeClick(data,node) {
            console.log(data,node);
            this.input2 = data.dept_name;
            localStorage.setItem("tid",data.id);
            var that = this;
            let formData = new FormData();
            formData.append('id',data.id);
            axios.post(this.baseUrl+'/ydjxzg/ht/getbumenry.action',formData)
                .then(function (res) {
                    console.log(res);
                    if(res.data.bmry == "无人员"){
                        that.tableData = "";
                        that.total = 0;
                    }else{
                        that.tableData = res.data.bmry;
                        that.total = res.data.bmry.length;
                    }
                });
        },
        tj(e){
            this.$refs.tmask.style.display = "block";
        },
        quxiao1(e){
            this.$refs.tmask.style.display = "none";
        },
        bj(e){
            this.$refs.bmask.style.display = "block";
        },
        quxiao2(e){
            this.$refs.bmask.style.display = "none";
        },
        sc(e){
            this.$refs.smask.style.display = "block";
        },
        quxiao3(e){
            this.$refs.smask.style.display = "none";
        },
        handleSizeChange(val) {
            console.log(`每页 ${val} 条`);
            this.pagesize = val;
        },
        handleCurrentChange(val) {
            console.log(`当前页: ${val}`);
            this.currpage = val;
        },
        tqd(){
            var that = this;
            var name = this.$refs.tj.value;
            var id = localStorage.getItem("tid")
            let formData = new FormData();
            formData.append('dept_name',name);
            formData.append('parent_id',id);
            if(name == "" || name == null){
                that.$message.error('添加内容不能为空')
            }else{
                axios.post(this.baseUrl+'/ydjxzg/ht/gotargetadd.action',formData)
                .then(function (res) {
                    console.log(res);
                    that.$refs.tmask.style.display = "none";
                    if(res.data.mess == true){
                        that.$message.success('添加成功')
                        that.reload()
                    }else{
                        that.$message.error('添加失败')
                    }
                });
            }
        },
        bqd(){
            var that = this;
            var name = this.$refs.bj.value;
            var id = localStorage.getItem("tid")
            let formData = new FormData();
            formData.append('dept_name',name);
            formData.append('id',id);
            if(name == "" || name == null){
                that.$message.error('编辑内容不能为空')
            }else{
                axios.post(this.baseUrl+'/ydjxzg/ht/editorganiza.action',formData)
                .then(function (res) {
                    console.log(res);
                    that.$refs.bmask.style.display = "none";
                    if(res.data.mess == true){
                        that.$message.success('编辑成功')
                        that.reload()
                    }else{
                        that.$message.error('编辑失败')
                    }
                });
            }
        },
        sqd(){
            var that = this;
            var id = localStorage.getItem("tid")
            let formData = new FormData();
            formData.append('id',id);
            axios.post(this.baseUrl+'/ydjxzg/ht/deleteOrganiza.action',formData)
            .then(function (res) {
                console.log(res);
                that.$refs.smask.style.display = "none";
                if(res.data.mess == true){
                    that.$message.success('删除成功')
                    that.reload()
                }else if(res.data.mess == false){
                    that.$message.error('删除失败')
                }else{
                    that.$message.error(res.data.mess)
                }
            });
        }
    },
    mounted(){
        var that = this;
        axios.post(this.baseUrl+'/ydjxzg/ht/organizalist.action')
            .then(function (res) {
                console.log(res);
                that.data = res.data.standardlist
            });
    }
}
</script>

<style scoped>
.all{
    width: 100%;
    height: 100%;
    overflow-x: hidden;
}
.back{
    float: left;
    margin: 50px 0 0 50px;
    width: 40%;
    height: 82%;
    background-color: #ffffff;
    box-shadow: 5px 5px 5px 5px #cccccc;
}
.table2{
    float: right;
    padding: 10px;
    margin-top: 50px;
    margin-right: 70px;
    width: 48%;
    height: 80%;
    background-color: #ffffff;
    box-shadow: 5px 5px 5px 5px #cccccc;
}
.change{
    width: 100%;
    height: 10%;
    border-bottom: 1px solid #cccccc;
}
.change ul{
    width: 100%;
    height: 100%;
}
.change ul li{
    float: left;
    width: 33.3%;
    text-align: center;
    line-height: 70px;
    cursor:pointer;
}
.change ul li i{
    color: #ffffff;
    padding: 3px;
}
.change ul li:nth-child(1) i{
    background-color: #358FE2;
}
.change ul li:nth-child(2) i{
    background-color: #7CB349;
}
.change ul li:nth-child(3) i{
    background-color: #E42739;
}
.tree{
    margin: 30px 0 0 15px;
}
/* .xq{
    float: right;
    margin: 30px 30px 0 0;
} */
.tmask{
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.3);
    z-index: 3;
    display: none;
}
.tj{
    position: relative;
    width: 420px;
    height: 200px;
    background-color: #ffffff;
    left: 50%;
    top: 50%;
    margin-left: -210px;
    margin-top: -100px;
}
.tj h3{
    padding: 10px 0 0 15px;
}
.input{
    width: 60%;
    margin: 30px 0 0 20%;
}
.btn1{
    margin: 30px 0 0 140px;
    cursor:pointer;
}
.bmask{
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.3);
    z-index: 3;
    display: none;
}
.bj{
    position: relative;
    width: 420px;
    height: 200px;
    background-color: #ffffff;
    left: 50%;
    top: 50%;
    margin-left: -210px;
    margin-top: -100px;
}
.bj h3{
    padding: 10px 0 0 15px;
}
.input2{
    width: 60%;
    margin: 30px 0 0 20%;
}
.btn2{
    margin: 30px 0 0 140px;
    cursor:pointer;
}
.smask{
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.3);
    z-index: 3;
    display: none;
}
.sc{
    position: relative;
    width: 420px;
    height: 200px;
    background-color: #ffffff;
    left: 50%;
    top: 50%;
    margin-left: -210px;
    margin-top: -100px;
}
.sc h3{
    text-align: center;
    padding-top: 30px
}
.btn3{
    margin: 45px 0 0 132px;
    cursor:pointer;
}
</style>



